.container{
  padding: 2% 18%;
  display: flex;
  flex-direction: column;
  height: 100%;
  border: 1.5px solid #000000;
  background-color: #F6F3F5;
  color: #000000;
}

html, body, #root, .App{
  /*height: 100%;*/
}

#name{
  font-family: 'Xanh Mono', monospace;
  font-style: italic;
  font-size: max(5vw, 3em);
  /*paint-order: stroke fill;*/
  -webkit-text-stroke-width: 0.5px;
  /*line-height: 0.2em;*/
  /*background-color: darkgreen;*/
  margin-bottom: -0.25em;
}

#myname{
  margin-top: -1.2em;
}


#words{
  margin-top: -1em;
  display: flex;
  /*flex: 1 1 0px;*/
  justify-content: space-between;
  grid-column-gap: 4em;
  /*flex-direction: column;*/
  font-family: 'Inter', sans-serif;
  /*background-color: blueviolet;*/
}

#left{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  flex: 1 1 0px;
  font-size: max(1.5vw, 1em);
  /*margin-top: -1em;*/
  /*line-height: 1.2em;*/
  /*background-color: skyblue;*/
}

.hiddenimg{
  display: none;
}

@media screen and (min-width: 1081px) {
  #myname:hover ~ .hiddenimg {
    display: block;
    position: absolute;
    top: 1.25em;
    left: 51vw;
    z-index: 2;
  }
  img{
    width: 31vw;
    height:auto;
  }
  #myname:hover{
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: black;
    color: #F6F3F5;
  }
}

@media screen and (max-width: 1048px) {
  #buttons{
    flex-direction: column;
    justify-content: stretch;
    text-align: center;
    align-items: stretch;
  }
  #button{
    text-align: center;
  }
}

@media screen and (min-width: 650px) {
  html, body, #root, .App{
  height: 100%;
  }
}

@media screen and (max-width: 1080px) {
  #words{
    flex-direction: column;
    grid-row-gap: 1em;
  }
  #buttons{
    grid-row-gap: 0.5em;
    flex-wrap: wrap;
  }
}


#left p{
  margin-top: 0.2em;
}

#right{
  /*flex-grow: 1;*/
  flex: 1 1 0px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  grid-row-gap: 1em;
  font-size: max(1.3vw, 0.9em);
  /*background-color: green;*/

}

#box{
  border: 2px solid #000000;
  padding: 0em 1em;
}

#title{
  font-family: 'Xanh Mono', monospace;
  font-style: italic;
  font-size: max(1vw, 1em);
  -webkit-text-stroke-width: 0.65px;
}

#link{
  /*text-decoration: none;*/
  color: #000000;
  /*font-weight: 400;*/
}
#link:hover{
  /*font-weight: 500;*/
  cursor: url(outlink.png) 15 15, move;
}
#buttons{
  padding-top: 2em;
  padding-bottom: 2em;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  /*grid-column-gap: 2em;*/
}

#button{
  background-color: #FFFFFF;
  color: #000000;
  padding:0.3em 2em;
  border-radius: 9em;
  font-size: max(1.25vw, 0.9em);
  font-weight: 500;
  border: 1.5px solid #000000;
  font-family: 'Inter', sans-serif;
  text-transform: uppercase;
}

a:hover, a:visited, a:link, a:active #button{
  text-decoration: none;
}
